/* SignupForm.css */

.signup-container {
    width: 100%;
    max-width: 400px;
    padding: 10px 40px;
    border: 1px solid #e1e1e1;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(214, 154, 154, 0.1);
    background-color: #fff;
}

.signup-container h2 {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
}

.signup-container h5 {
    font-size: 12px;
    margin-bottom: 10px;
    text-align: center;
    color: #f10707;
}

.form-group {
    margin-bottom: 20px;
}

label {
    display: block;
    font-size: 16px;
    margin-bottom: 8px;
    color: #555;
}

input[type="text"],
input[type="email"],
input[type="password"] {
    width: 100%;
    padding: 10px 10px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    font-size: 16px;
    color: #333;
}

.countryCode {
    width: 100%;
    padding: 10px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    font-size: 16px;
    color: #333;
    appearance: none;
    background-color: #fff;
    background-image: linear-gradient(45deg, transparent 50%, #d1d1d1 50%), linear-gradient(135deg, #d1d1d1 50%, transparent 50%);
    background-position: calc(100% - 20px) center, calc(100% - 15px) center;
    background-size: 5px 5px, 5px 5px;
    background-repeat: no-repeat;
    cursor: pointer;
    outline: none;
}

.error {
    border-color: red;
}

.error-message {
    color: red;
    font-size: 14px;
    margin-top: 5px;
}

.signUpSubmit {
    display: block;
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    transition: background-color 0.3s;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

button:hover {
    background-color: #0056b3;
}