.buySellContainerWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.buySell-container {
    width: 100%;
    max-width: 400px;
    padding: 20px 30px;
    border: 1px solid #e1e1e1;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(214, 154, 154, 0.1);
    background-color: #fff;
}

.buySellButtonWrapper {
    display: flex;
    justify-content: space-evenly;
    padding: 20px 0px;
}

.buySell-container h2 {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
}

.buySell-container h5 {
    font-size: 12px;
    margin-bottom: 10px;
    text-align: center;
    color: #f10707;
}

.buySell-form-group {
    margin-bottom: 20px;
}

.buySell-label {
    display: block;
    font-size: 16px;
    margin-bottom: 8px;
    color: #555;
}

.buySell-selectField {
    width: 100%;
    padding: 10px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    font-size: 16px;
    color: #333;
    appearance: none;
    background-color: #fff;
    background-image: linear-gradient(45deg, transparent 50%, #d1d1d1 50%), linear-gradient(135deg, #d1d1d1 50%, transparent 50%);
    background-position: calc(100% - 20px) center, calc(100% - 15px) center;
    background-size: 5px 5px, 5px 5px;
    background-repeat: no-repeat;
    cursor: pointer;
    outline: none;
}

.buySell-error {
    border-color: red;
}

.buySell-error-message {
    color: red;
    font-size: 14px;
    margin-top: 5px;
}

.findOffersSubmit {
    display: block;
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    transition: background-color 0.3s;
}

.buySell-input input {
    width: 100%;
    padding: 10px 10px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    font-size: 16px;
    color: #333;
}

.buy-button,
.sell-button {
    display: inline-block;
    padding: 10px 20px;
    width: 100px;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

.buy-button:hover,
.sell-button:hover {
    background-color: transparent;
    color: #333;
}

.buy-button:focus,
.sell-button:focus {
    outline: none;
}

.activeButton {
    background-color: #0056b3;
    color: white;
}

.deActiveButton {
    background-color: #ccc;
    color: black;
}